import React from "react";
import styled from "styled-components";
import Assing from "../../images/assignment.png";
import Test from "../../images/quiz.png";
import Attendance from "../../images/attendance.png";
import Lab from "../../images/nanotechnology.png";

const AddAttendance = React.lazy(() => import("./Attendance/AddAttendance"));

const AddAssignment = React.lazy(() =>
  import("../MarksEntry/Assignment/AddAssignment")
);
const AddQuizz = React.lazy(() => import("../MarksEntry/Quizz/AddQuizz"));

const AddLabPerformance = React.lazy(() =>
  import("../MarksEntry/LabPerform/AddLabPerformance")
);

const MarksActionContainer = ({ classInfo, back, setBack }) => {
  const [openAssign, setOpenAssign] = React.useState(false);
  const [openQuizz, setOpenQuizz] = React.useState(false);
  const [openLab, setOpenLab] = React.useState(false);
  const [openAttendance, setOpenAttendance] = React.useState(false);
  const [label, setLabel] = React.useState(null);

  React.useEffect(() => {
    if (!back) {
      setOpenAssign(false);
      setOpenQuizz(false);
      setOpenAttendance(false);
      setOpenLab(false);
      setLabel(null);
    }
  }, [back]);

  window.onclick = (e) => {
    if (e.target.id === "assignParent") {
      document.getElementById("assign").style.display = "block";
    } else {
      let assign = document.getElementById("assign");
      if (assign !== null) {
        assign.style.display = "none";
      }
    }
    if (e.target.id === "quizzParent") {
      document.getElementById("quizz").style.display = "block";
    } else {
      let quizz = document.getElementById("quizz");
      if (quizz !== null) {
        quizz.style.display = "none";
      }
    }
  };

  return (
    <React.Fragment>
      {!openAssign && !openAttendance && !openLab && !openQuizz && (
        <MarkesGridButtonPanel
          setOpenLab={setOpenLab}
          setOpenQuizz={setOpenQuizz}
          setOpenAttendance={setOpenAttendance}
          setOpenAssign={setOpenAssign}
          setLabel={setLabel}
          setBack={setBack}
        />
      )}
      {openAttendance && (
        <AddAttendance
          classInfo={classInfo}
          openBox={openAttendance}
          setOpenBox={setOpenAttendance}
          setBack={setBack}
        />
      )}
      {openAssign && label !== null && (
        <AddAssignment
          openBox={openAssign}
          setOpenBox={setOpenAssign}
          classInfo={classInfo}
          label={label}
          setLabel={setLabel}
          setBack={setBack}
        />
      )}

      {openQuizz && label !== null && (
        <AddQuizz
          openBox={openQuizz}
          setOpenBox={setOpenQuizz}
          classInfo={classInfo}
          label={label}
          setLabel={setLabel}
          setBack={setBack}
        />
      )}
      {openLab && (
        <AddLabPerformance
          openBox={openLab}
          setOpenBox={setOpenLab}
          classInfo={classInfo}
          setBack={setBack}
        />
      )}
    </React.Fragment>
  );
};

export default MarksActionContainer;

function MarkesGridButtonPanel({
  setOpenLab,
  setOpenQuizz,
  setOpenAssign,
  setOpenAttendance,
  setLabel,
  setBack,
}) {
  return (
    <ActionPanel>
      <Title>Choose Option</Title>
      <ButtonPanelGrid>
        <GridButton>
          <Image src={Assing} alt="Import File" />
          <Text>Assignment</Text>

          <Button id="assignParent" />
          <Option id="assign">
            <Ul style={{ padding: 0 }}>
              <Li
                onClick={() => {
                  setBack(true);
                  setLabel(1);
                  setOpenAssign(true);
                }}
              >
                Assignment-1
              </Li>
              <Li
                onClick={() => {
                  setBack(true);
                  setLabel(2);
                  setOpenAssign(true);
                }}
              >
                Assignment-2
              </Li>
              <Li
                onClick={() => {
                  setBack(true);
                  setLabel(3);
                  setOpenAssign(true);
                }}
              >
                Assignment-3
              </Li>
            </Ul>
          </Option>
        </GridButton>
        <GridButton>
          <Image src={Test} alt="Import File" />
          <Text>Quizzes</Text>
          <Button id="quizzParent" />
          <Option id="quizz">
            <Ul style={{ padding: 0 }}>
              <Li
                onClick={() => {
                  setBack(true);
                  setLabel(1);
                  setOpenQuizz(true);
                }}
              >
                Quizz-1
              </Li>
              <Li
                onClick={() => {
                  setBack(true);
                  setLabel(2);
                  setOpenQuizz(true);
                }}
              >
                Quizz-2
              </Li>
            </Ul>
          </Option>
        </GridButton>
        <GridButton
          onClick={() => {
            setBack(true);
            setOpenLab(true);
            setLabel(null);
          }}
        >
          <Image src={Lab} alt="Import File" />
          <Text>Lab Performance</Text>
        </GridButton>
        <GridButton
          onClick={() => {
            setLabel(null);
            setBack(true);
            setOpenAttendance(true);
          }}
        >
          <Image src={Attendance} alt="Sample File" />
          <Text>Attendance</Text>
        </GridButton>
      </ButtonPanelGrid>
    </ActionPanel>
  );
}

const ActionPanel = styled.div`
  width: 550px;
  border-radius: 5px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: 560px) {
    width: 95%;
  }
`;
const ButtonPanelGrid = styled.div`
  display: flex;
  align-items: stretch;
  justify-items: stretch;
  padding: 1rem;
  flex-wrap: wrap;
  justify-content: center;
`;
const GridButton = styled.div`
  width: 130px;
  height: 150px;
  cursor: pointer;
  margin: 1rem;
  overflow: hidden;
  border: 1px solid rgba(3, 3, 3, 0.3);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
`;

const Image = styled.img`
  width: 40%;
`;

const Title = styled.h1`
  font-size: 1.2rem;
  font-weight: 500;
  text-align: center;
`;

const Text = styled.p`
  text-align: center;
  margin-bottom: 0;
  margin-top: 5px;
  font-size: 0.9rem;
`;

const Option = styled.div`
  width: 130px;
  padding: 0.5rem 0.1rem;
  border-radius: 3px;
  position: absolute;
  background: #fff;
  box-shadow: 1px 1px 1px 1px rgba(3, 3, 3, 1);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
`;

const Li = styled.li`
  font-size: 0.9rem;
  padding: 0.5rem 0.2rem;
  list-style-type: none;
  font-weight: 500;
  cursor: pointer;
  text-align: center;
  width: 100%;
  border-radius: 5px;
  &:hover {
    background: rgba(199, 236, 238, 0.5);
  }
`;

const Button = styled.button`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: transparent;
`;

const Ul = styled.ul`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
`;
